import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import * as Yup from "yup";
import axios from "../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import {
  Button,
  FormControlLabel,
  Radio,
  LinearProgress,
  Grid,
  RadioGroup,
  FormControl,
  FormLabel,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import { TextField, Select } from "formik-material-ui";
import { connect } from "react-redux";
import LinkIcon from "@material-ui/icons/Link";
import AttachmentIcon from "@material-ui/icons/AttachFile";
import SubjectIcon from "@material-ui/icons/Subject";
import ToggleButton from "@material-ui/lab/ToggleButton";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";

import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { now } from "moment";

const mapStateToProps = (state) => ({
  Dept_ID: state.Dept_ID,
  collegeID: state.collegeID,
});
class Notifications extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDate: new Date(now()),
      file: [],
      value: "Others",
      noticeList: [],
      noticeType: "Text",
      selectedNotice: "",
      title: "",
      pubdate: "",
      category: "",
      discription: "",
    };
  }
  handleDateChange(date) {
    this.setState({
      selectedDate: new Date(date).toISOString(),
    });
  }
  componentDidMount() {
    axios
      .get(`notifications/notification/all/` + this.props.collegeID, {})
      .then((res) => {
        this.setState({
          noticeList: res.data,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    const handleChangeNotice = (event) => {
      let id = event.target.value;
      this.setState({
        selectedNotice: id,
      });
      axios
        .get("notifications/notice/" + id)
        .then((res) => {
          this.setState({
            title: res.data[0].title,
            selectedDate: res.data[0].pubdate,
            category: res.data[0].category,
            discription: res.data[0].discription,
          });
        })
        .catch((error) => {
          alert(error);
        });
    };
    const handleChange = (event) => {
      this.setState({ category: event.target.value });
    };
    async function uploadFile(file1, callback) {
      let bodyFormData = new FormData();
      bodyFormData.append("file1", file1);
      const request = axios
        .post("upload", bodyFormData, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          //alert(res.data.fileName)
          let filena = res.data.fileName;
          request.then(() => callback(filena));
          request.catch((err) => callback(err.response));
        });
    }

    const fileSelected = (event) => {
      this.setState({
        file: event.target.files[0],
      });
    };
    const handleAlignment = (event, newAlignment) => {
      this.setState({
        noticeType: newAlignment,
      });
    };
    return (
      <Grid spacing={6}>
        <Grid item lg={12} xs={12}>
          <h1 class="card-title">College Notification </h1>
        </Grid>
        <Formik
          initialValues={{
            title: this.state.title,
            filename: "",
            link: "na",
            pubdate: this.state.pubdate,
            category: this.state.category,
            discription: this.state.discription,
            isactive: 1,
            notificationtype: 1,
          }}
          enableReinitialize
          validationSchema={Yup.object({
            title: Yup.string()
              .max(200, "Must be 200 characters or less")
              .min(3, "Must be at least 3 characters")
              .required("Notification Title Can't be Blank"),
          })}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            let fn = "";
            if (this.state.noticeType == "attachment") {
              uploadFile(this.state.file, (response) => {
                // your code after API response
                fn = response;
                setTimeout(() => {
                  setSubmitting(false);
                  values.pubdate = this.state.selectedDate;
                  values.filename = fn;
                  values.category = this.state.value;
                  let rowID = this.state.selectedNotice;
                  axios
                    .post("notifications/notifications/update/" + rowID, values)
                    .then((res) => {
                      if (res.data.flag === "success") {
                        alert("The Notification Updated Successfully");
                      }
                    })
                    .catch((error) => {
                      alert(error);
                      resetForm();
                    });
                  resetForm();
                }, 500);
              });
            } else {
              setTimeout(() => {
                setSubmitting(false);
                values.pubdate = this.state.selectedDate;
                values.category = this.state.category;
                let rowID = this.state.selectedNotice;
                axios
                  .post("notifications/notifications/update/" + rowID, values)
                  .then((res) => {
                    if (res.data.flag === "success") {
                      alert("The Notification Updated Successfully");
                    }
                  })
                  .catch((error) => {
                    alert(error);
                    resetForm();
                  });
              });
            }
          }}
        >
          {({ submitForm, isSubmitting, values }) => (
            <Form>
              <Grid item lg={12} md={12}>
                <div class="formspacer " />
                <FormControl
                  variant="outlined"
                  fullWidth
                  required
                  margin="dense"
                >
                  <InputLabel htmlFor="Notice">Select Notice</InputLabel>
                  <Field
                    component={Select}
                    name="Notice"
                    label="Select Notice"
                    onChange={handleChangeNotice}
                  >
                    {this.state.noticeList.map((notice) => (
                      <MenuItem key={notice.id} value={notice.id}>
                        {notice.title}
                      </MenuItem>
                    ))}
                  </Field>
                </FormControl>
              </Grid>
              <Grid item lg={12} md={12}>
                <FormControl component="fieldset" name="category">
                  <FormLabel component="legend">Notification Type</FormLabel>
                  <RadioGroup
                    aria-label="exam"
                    name="noticetype"
                    row
                    value={this.state.category}
                    onChange={handleChange}
                  >
                    <FormControlLabel
                      value="exam"
                      control={<Radio />}
                      label="Exam"
                    />
                    <FormControlLabel
                      value="admission"
                      control={<Radio />}
                      label="Admission"
                    />
                    <FormControlLabel
                      value="Others"
                      control={<Radio />}
                      label="Others"
                    />
                  </RadioGroup>
                </FormControl>
              </Grid>
              <Grid lg={12} xs={12}>
                <div class="formspacer " />
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <DateTimePicker
                    value={this.state.selectedDate}
                    onChange={(date) => this.handleDateChange(date)}
                    label="Select Notification Date & Time"
                    inputVariant="outlined"
                    fullWidth
                    format="dd/MM/yyyy hh:mm a"
                  />
                </MuiPickersUtilsProvider>

                <div class="formspacer " />
              </Grid>
              <Grid item lg={12} md={12}>
                <Field
                  component={TextField}
                  name="title"
                  type="text"
                  label="Notification Title"
                  variant="outlined"
                  fullWidth
                  required
                />
              </Grid>
              <Grid item lg={12} md={12}>
                <div class="formspacer " />
                <Field
                  component={TextField}
                  name="discription"
                  type="text"
                  label="Notification Description"
                  variant="outlined"
                  fullWidth
                  required
                />
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                <ToggleButtonGroup
                  value={this.state.noticeType}
                  exclusive
                  onChange={handleAlignment}
                  aria-label="text alignment"
                  size="large"
                >
                  <ToggleButton value="Link" aria-label="Link">
                    <LinkIcon />
                  </ToggleButton>
                  <ToggleButton value="attachment" aria-label="attachment">
                    <AttachmentIcon />
                  </ToggleButton>
                  <ToggleButton value="Text" aria-label="Text">
                    <SubjectIcon />
                  </ToggleButton>
                </ToggleButtonGroup>
              </Grid>
              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                {this.state.noticeType == "attachment" ? (
                  <input
                    onChange={fileSelected}
                    type="file"
                    accept="application/pdf"
                  ></input>
                ) : null}
                {this.state.noticeType == "Link" ? (
                  <Field
                    component={TextField}
                    name="filename"
                    type="text"
                    label="Notification Link"
                    variant="outlined"
                    fullWidth
                    required
                  />
                ) : null}
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                {isSubmitting && <LinearProgress />}
                <Button
                  variant="contained"
                  color="primary"
                  disabled={isSubmitting}
                  onClick={submitForm}
                  fullWidth
                >
                  Update
                </Button>
              </Grid>
            </Form>
          )}
        </Formik>
      </Grid>
    );
  }
}

export default connect(mapStateToProps)(Notifications);
